import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ScholarshipPage } from './scholarship.page';

const routes: Routes = [
  {
    path: '',
    component: ScholarshipPage
  },
  {
    path: 'scholarship-home',
    loadChildren: () => import('./scholarship-home/scholarship-home.module').then( m => m.ScholarshipHomePageModule)
  },
  {
    path: 'scholarship-test',
    loadChildren: () => import('./scholarship-test/scholarship-test.module').then( m => m.ScholarshipTestPageModule)
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ScholarshipPageRoutingModule {}
